:root {
  --innerValue: 1496;
  --inner: 1496px;
  --padding-pc: 40px;
  --padding-sp: 35px;
  --base-color: #3b3b3b;
  --white: #ffffff;
  --black: #000000;
  --yellow: #fef30b;
  --yellow-light: #fff982;
  --pink: #efb9b9;
  --green: #0ba500;
  --green-light: #b1f19e;
  --gray: #707070;
  --light: 300;
  --normal: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
  --ex-bold: 800;
  --sans-family: "Noto Sans JP", sans-serif;
  --duration: 0.3s ease;
}

.hoverOpacity {
  transition: opacity var(--duration);
}
@media (any-hover: hover) {
  .hoverOpacity:hover {
    opacity: 0.7;
  }
}

.footer {
  box-sizing: border-box;
  font-family: var(--sans-family);
  text-align: left;
  padding-block-end: clamp(38px, calc(-3.609403255px + 10.6690777577vw), 156px);
}
@media screen and (max-width:834px) {
  .footer {
    background-color: color-mix(in srgb, var(--gray) 12%, transparent);
  }
}
.footer * {
  box-sizing: border-box;
}

.footer__inner {
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  margin-inline: auto;
  padding-inline: var(--padding-pc);
}
@media screen and (max-width:834px) {
  .footer__inner {
    padding-inline: var(--padding-sp);
  }
}

.footer__contents {
  border-top: 1px solid var(--gray);
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  padding-block-start: clamp(22px, calc(-12.5569620253px + 8.8607594937vw), 120px);
}
@media screen and (max-width:834px) {
  .footer__contents {
    border-top: none;
    display: block;
  }
}

.footer__main {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  column-gap: 20px;
  width: max(600px, 62%);
  padding-inline: min(130px, 6vw) 50px;
}
@media screen and (max-width:834px) {
  .footer__main {
    flex-direction: column;
    width: 100%;
  }
}

@media screen and (max-width:834px) {
  .footer__mainWrap {
    display: contents;
  }
}

@media screen and (max-width:834px) {
  .footer__head {
    order: 1;
    text-align: center;
  }
}

.footer__logo {
  width: clamp(108px, calc(42.4122965642px + 16.8173598553vw), 294px);
}
@media screen and (max-width:834px) {
  .footer__logo {
    display: inline-block;
  }
}

.footer__body {
  margin-block-start: 9px;
}
@media screen and (max-width:834px) {
  .footer__body {
    margin-block-start: 30px;
    order: 3;
  }
}

.footer__snsTitle {
  font-size: 20px;
  font-family: var(--sans-family);
  font-weight: var(--ex-bold);
  line-height: 1.45;
  letter-spacing: 0.067em;
}
@media screen and (max-width:834px) {
  .footer__snsTitle {
    padding-block-end: 10px;
    border-bottom: 1px solid var(--gray);
    text-align: center;
  }
}

.footer__snsLists {
  margin-block-start: 14px;
}
@media screen and (max-width:834px) {
  .footer__snsLists {
    margin-block-start: 30px;
  }
}

@media screen and (max-width:834px) {
  .footer__list {
    text-align: center;
  }
}

.footer__list.mt {
  margin-block-start: 50px;
}

.footer__snsList + .footer__snsList {
  margin-block-start: 10px;
}
@media screen and (max-width:834px) {
  .footer__snsList + .footer__snsList {
    margin-block-start: 20px;
  }
}

.footer__snsText {
  font-size: clamp(12px, calc(9.1790235081px + 0.7233273056vw), 20px);
}
@media screen and (max-width:834px) {
  .footer__snsText {
    text-align: center;
  }
}

.footer__snsLink {
  display: block;
  width: 45px;
  margin-block-start: 10px;
  text-align: center;
}
@media screen and (max-width:834px) {
  .footer__snsLink {
    margin-inline: auto;
  }
}

@media screen and (max-width:834px) {
  .footer__bottom {
    order: 2;
  }
}

.footer__adBlock + .footer__adBlock {
  margin-block-start: 15px;
}

.footer__adText {
  font-size: clamp(12px, calc(9.1790235081px + 0.7233273056vw), 20px);
  line-height: 1.45;
}
@media screen and (max-width:834px) {
  .footer__adText {
    text-align: center;
  }
}

.footer__adTel {
  font-size: clamp(20px, calc(14.0054249548px + 1.5370705244vw), 37px);
  line-height: 1.4594594595;
}
@media screen and (max-width:834px) {
  .footer__adTel {
    text-align: center;
  }
}

.footer__sub {
  width: max(400px, 38%);
  padding-inline: 50px;
  border-right: 1px solid var(--gray);
}
@media screen and (max-width:834px) {
  .footer__sub {
    display: none;
  }
}

.footer__listWrap {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 60px;
}

.footer__list {
  margin-block-start: 10px;
}
.footer__list a {
  font-size: 20px;
  line-height: 1.45;
  letter-spacing: 0.067em;
  color: var(--base-color);
  text-decoration: none;
}

.footer__subLists {
  padding-left: 2em;
}

.footer__subList a {
  font-size: 20px;
  line-height: 1.45;
  letter-spacing: 0.067em;
  color: var(--base-color);
  text-decoration: none;
}

.footer__copy {
  font-size: clamp(12px, calc(9.1790235081px + 0.7233273056vw), 20px);
  line-height: 1.45;
  text-align: center;
  margin-block-start: clamp(30px, calc(-54.2766726944px + 21.609403255vw), 269px);
}

.sub {
  color: var(--base-color);
}
.sub .inner {
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  margin-inline: auto;
  padding-inline: var(--padding-pc);
}
@media screen and (max-width:834px) {
  .sub .inner {
    padding-inline: var(--padding-sp);
  }
}